/**
 * Creates the Current Cart of the customer.
 * @param cart - Cart to be created.
 * @internal
 * @documentationMaturity preview
 * @requiredField cart
 * @requiredField options.catalogItems.catalogReference
 * @requiredField options.catalogItems.quantity
 * @requiredField options.catalogItems.selectedMembership._id
 * @requiredField options.catalogItems.selectedMembership.appId
 * @requiredField options.customItems.name
 * @requiredField options.customItems.pricing
 * @requiredField options.customItems.quantityInfo
 * @requiredField options.customItems.quantityInfo.requestedQuantity
 * @permissionId ecom:v2:cart:create_cart
 * @permissionId ECOM.ADMIN_MODIFY_CARTS
 * @applicableIdentity APP
 * @fqn wix.ecom.cart.v2.CurrentCartService.CreateCurrentCart
 */
export async function createCurrentCart(
  cart: Cart,
  options?: NonNullablePaths<
    CreateCurrentCartOptions,
    | `catalogItems.${number}.catalogReference`
    | `catalogItems.${number}.quantity`
    | `catalogItems.${number}.selectedMembership._id`
    | `catalogItems.${number}.selectedMembership.appId`
    | `customItems.${number}.attributes.descriptionLines.${number}.name`
    | `customItems.${number}.name`
    | `customItems.${number}.pricing`
    | `customItems.${number}.pricing.price`
    | `customItems.${number}.quantityInfo`
    | `customItems.${number}.quantityInfo.requestedQuantity`,
    7
  >
): Promise<
  NonNullablePaths<
    CreateCurrentCartResponse,
    | `cart._id`
    | `cart.lineItems`
    | `cart.lineItems.${number}._id`
    | `cart.lineItems.${number}.name.original`
    | `cart.lineItems.${number}.quantityInfo.confirmedQuantity`
    | `cart.lineItems.${number}.quantityInfo.requestedQuantity`
    | `cart.lineItems.${number}.quantityInfo.fixedQuantity`
    | `cart.lineItems.${number}.pricing.unitPrice.amount`
    | `cart.lineItems.${number}.pricing.unitPrice.convertedAmount`
    | `cart.lineItems.${number}.pricing.unitPrice.formattedAmount`
    | `cart.lineItems.${number}.pricing.unitPrice.formattedConvertedAmount`
    | `cart.lineItems.${number}.pricing.priceDescription.original`
    | `cart.lineItems.${number}.pricing.priceUndetermined`
    | `cart.lineItems.${number}.source.customLineItem`
    | `cart.lineItems.${number}.source.catalogReference.catalogItemId`
    | `cart.lineItems.${number}.source.catalogReference.appId`
    | `cart.lineItems.${number}.attributes.itemType.preset`
    | `cart.lineItems.${number}.attributes.itemType.custom`
    | `cart.lineItems.${number}.attributes.physicalProperties.shippable`
    | `cart.lineItems.${number}.taxConfig.taxableAddress.addressType`
    | `cart.lineItems.${number}.paymentConfig.savePaymentMethod`
    | `cart.lineItems.${number}.paymentConfig.selectedMembership._id`
    | `cart.lineItems.${number}.paymentConfig.selectedMembership.appId`
    | `cart.lineItems.${number}.paymentConfig.paymentOption`
    | `cart.lineItems.${number}.status`
    | `cart.lineItems.${number}.membersOnly`
    | `cart.discounts`
    | `cart.discounts.${number}._id`
    | `cart.discounts.${number}.source.sourceType`
    | `cart.discounts.${number}.scope`
    | `cart.discounts.${number}.status`
    | `cart.source.channelType`
    | `cart.source.externalReferences`
    | `cart.source.externalReferences.${number}.appId`
    | `cart.source.createdBy.visitorId`
    | `cart.source.createdBy.memberId`
    | `cart.source.createdBy.userId`
    | `cart.source.createdBy.appId`
    | `cart.source.customContentReference.appId`
    | `cart.source.customContentReference.componentId`
    | `cart.siteInfo.weightUnit`
    | `cart.siteInfo.languageCode`
    | `cart.siteInfo.currencyCode`
    | `cart.customerInfo.visitorId`
    | `cart.customerInfo.memberId`
    | `cart.customerInfo.userId`
    | `cart.customerInfo.languageCode`
    | `cart.customerInfo.currencyCode`
    | `cart.deliveryInfo.address.streetAddress.number`
    | `cart.deliveryInfo.address.streetAddress.name`
    | `cart.deliveryInfo.recipient.vatId._id`
    | `cart.deliveryInfo.recipient.vatId.type`
    | `cart.deliveryInfo.method.appId`
    | `cart.deliveryInfo.method.code`
    | `cart.deliveryInfo.method.pickup`
    | `cart.taxInfo.pricesIncludeTax`
    | `cart.paymentInfo.giftCards`
    | `cart.paymentInfo.giftCards.${number}._id`
    | `cart.paymentInfo.giftCards.${number}.obfuscatedCode`
    | `cart.paymentInfo.giftCards.${number}.appId`
    | `cart.paymentInfo.currencyCode`
    | `cart.status`
    | `cart.customSettings.lockGiftCard`
    | `cart.customSettings.lockCouponCode`
    | `cart.customSettings.policyAgreementCheckboxDisabled`
    | `cart.customSettings.manualPaymentDisabled`,
    7
  >
> {
  // @ts-ignore
  const { httpClient, sideEffects } = arguments[2] as {
    httpClient: HttpClient;
    sideEffects?: any;
  };

  const payload = transformPaths(
    renameKeysFromSDKRequestToRESTRequest({
      cart: cart,
      catalogItems: options?.catalogItems,
      customItems: options?.customItems,
      couponCodes: options?.couponCodes,
      giftCardCodes: options?.giftCardCodes,
    }),
    [
      {
        transformFn: transformSDKImageToRESTImage,
        paths: [
          { path: 'cart.lineItems.source.catalogOverrideFields.image' },
          { path: 'cart.lineItems.attributes.image' },
          { path: 'catalogItems.catalogOverrideFields.image' },
          { path: 'customItems.attributes.image' },
        ],
      },
      {
        transformFn: transformSDKPageURLV2ToRESTPageURLV2,
        paths: [
          { path: 'cart.lineItems.attributes.url' },
          { path: 'customItems.attributes.url' },
        ],
      },
      {
        transformFn: transformSDKAddressToRESTAddress,
        paths: [
          { path: 'cart.deliveryInfo.address' },
          { path: 'cart.paymentInfo.billingAddress' },
        ],
      },
    ]
  );

  const reqOpts = ambassadorWixEcomV2Cart.createCurrentCart(payload);

  sideEffects?.onSiteCall?.();
  try {
    const result = await httpClient.request(reqOpts);
    sideEffects?.onSuccess?.(result);

    return renameKeysFromRESTResponseToSDKResponse(
      transformPaths(result.data, [
        {
          transformFn: transformRESTImageToSDKImage,
          paths: [
            { path: 'cart.lineItems.source.catalogOverrideFields.image' },
            { path: 'cart.lineItems.attributes.image' },
          ],
        },
        {
          transformFn: transformRESTPageURLV2ToSDKPageURLV2,
          paths: [{ path: 'cart.lineItems.attributes.url' }],
        },
        {
          transformFn: transformRESTAddressToSDKAddress,
          paths: [
            { path: 'cart.deliveryInfo.address' },
            { path: 'cart.paymentInfo.billingAddress' },
          ],
        },
      ])
    )!;
  } catch (err: any) {
    const transformedError = sdkTransformError(
      err,
      {
        spreadPathsToArguments: {},
        explicitPathsToArguments: {
          cart: '$[0]',
          catalogItems: '$[1].catalogItems',
          customItems: '$[1].customItems',
          couponCodes: '$[1].couponCodes',
          giftCardCodes: '$[1].giftCardCodes',
        },
        singleArgumentUnchanged: false,
      },
      ['cart', 'options']
    );
    sideEffects?.onError?.(err);

    throw transformedError;
  }
}

